@import '/src/styles/responsive.scss';

.wrapper {
    width: $desktop-width;

    @include mobile {
        width: 100%;
    }

    @include tablet {
        width: $tablet-width;
    }

    background-color: white;

    &+.wrapper {
        margin-top: 16px;

        @include mobile {
            margin-top: 8px;
        }

        @include tablet {
            margin-top: 8px;
        }
    }
}

.title {
    text-transform: uppercase;
    padding-bottom: 16px;
    font-size: 1.5rem;
    color: orangered;
}