// navbar
.wrapper {
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
}

.link {
    display: flex;
    text-transform: uppercase;
    text-decoration: none;
    color: var(--color);
    padding-bottom: 4px;

    &+.link {
        margin-left: 32px;
    }

    &.active {
        border-bottom: 1px solid;
        color: white;
    }
}

.icon {
    margin-right: 4px;
    color: none;
    display: none;
}